<script type="application/ecmascript">
import CommonBottomHref from '@/components/BottomHref';
import {sendLog} from "@/utils/log-util";
import {ZbaseTopAdv} from "zbase-popup-component";
import SetPasswd from "./SetPasswd";
import {getBrowser} from "../utils/browser-util";
import {storage} from "webextension-polyfill";
import moreToolsVue from '@/pages/moreTools/MoreTools.vue'

export default {
  name: 'App',
  components: {
    ZbaseTopAdv,
    CommonBottomHref,
    SetPasswd,
    moreToolsVue
  },
  data() {
    return {
      value: false,
      version: '1.0.0',
      on: false,
      messages: false,
      messagesPreview: false,
      mediaPreview: false,
      mediaGallery: false,
      textInput: false,
      profilePic: false,
      name: false,
      noDelay: false,
      unblurActive: false,
      lockScreen: false,
      browserType: "Chrome",
      changePWD: false,
      TopRotation: true,
      // 当前标签页面
      currentName: 'main-page',
      mainPageText:"Home",
      moreToolsText:"More Tools"
    }
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event)
    },
    getVersion() {
      this.version = chrome.runtime.getManifest().version
    },
    async setCurrentStateInPop() {
      const data = await storage.sync.get([
        'on',
        'messages',
        'messagesPreview',
        'mediaPreview',
        'mediaGallery',
        'textInput',
        'profilePic',
        'name',
        'noDelay',
        'lockScreen',
        'unblurActive'
      ]);

      this.on = data.on
      this.messages = data.messages
      this.messagesPreview = data.messagesPreview
      this.mediaPreview = data.mediaPreview
      this.mediaGallery = data.mediaGallery
      this.textInput = data.textInput
      this.profilePic = data.profilePic
      this.name = data.name
      this.noDelay = data.noDelay
      this.lockScreen = data.lockScreen
      this.unblurActive = data.unblurActive
    },
    async onChange() {
      storage.sync.set({on: this.on})
      const result = await chrome.tabs.query({active: true, currentWindow: true});
      console.log(result);
      this.refreshScript()
      if (this.on) {
        sendLog(100101, {s_status: 1})
      } else {
        sendLog(100101, {s_status: 2})
      }
    },
    messagesChange() {
      chrome.storage.sync.set({messages: this.messages})
      this.refreshScript()
      if (this.messages) {
        sendLog(100102, {s_status: 1})
      } else {
        sendLog(100102, {s_status: 2})
      }
    },
    messagesPreviewChange() {
      chrome.storage.sync.set({messagesPreview: this.messagesPreview})
      this.refreshScript()
      if (this.messagesPreview) {
        sendLog(100103, {s_status: 1})
      } else {
        sendLog(100103, {s_status: 2})
      }
    },
    mediaPreviewChange() {
      chrome.storage.sync.set({mediaPreview: this.mediaPreview})
      this.refreshScript()
      if (this.mediaPreview) {
        sendLog(100104, {s_status: 1})
      } else {
        sendLog(100104, {s_status: 2})
      }
    },
    mediaGalleryChange() {
      chrome.storage.sync.set({mediaGallery: this.mediaGallery})
      this.refreshScript()
      if (this.mediaGallery) {
        sendLog(100105, {s_status: 1})
      } else {
        sendLog(100105, {s_status: 2})
      }
    },
    textInputChange() {
      chrome.storage.sync.set({textInput: this.textInput})
      this.refreshScript()
      if (this.textInput) {
        sendLog(100106, {s_status: 1})
      } else {
        sendLog(100106, {s_status: 2})
      }
    },
    profilePicChange() {
      chrome.storage.sync.set({profilePic: this.profilePic})
      this.refreshScript()
      if (this.profilePic) {
        sendLog(100107, {s_status: 1})
      } else {
        sendLog(100107, {s_status: 2})
      }
    },
    nameChange() {
      chrome.storage.sync.set({name: this.name})
      this.refreshScript()
      if (this.name) {
        sendLog(100108, {s_status: 1})
      } else {
        sendLog(100108, {s_status: 2})
      }
    },
    noDelayChange() {
      chrome.storage.sync.set({noDelay: this.noDelay})
      this.refreshScript()
      if (this.noDelay) {
        sendLog(100109, {s_status: 1})
      } else {
        sendLog(100109, {s_status: 2})
      }
    },
    unblurActiveChange() {
      chrome.storage.sync.set({unblurActive: this.unblurActive})
      this.refreshScript()
      if (this.unblurActive) {
        sendLog(100111, {s_status: 1})
      } else {
        sendLog(100111, {s_status: 2})
      }
    },
    refreshScript() {
      chrome.tabs.query({url: '*://*.whatsapp.com/*'}, function (tabs) {
        console.log("加载load.js")
        if (tabs.length !== 0)
          tabs.forEach(function (tab) {
            console.log("tab遍历加载load.js")
            chrome.scripting.executeScript({
              target: {tabId: tab.id},
              files: ['load.js'],
            });
          });
      });
    },
    openOrCheckWaTab(event) {
      chrome.tabs.query({currentWindow: true}, function (tabs) {
        if (tabs.length > 0) {
          const urlArr = tabs.filter((item) => item.url);
          let whatsappTab = urlArr.find(
            (e) =>
                  e.url.startsWith("http://web.whatsapp.com/") ||
                  e.url.startsWith("https://web.whatsapp.com/")
          );
          if (typeof whatsappTab !== "undefined") {
            chrome.tabs.highlight(
                {tabs: whatsappTab.index, windowId: whatsappTab.windowId},
              function () {
                  chrome.windows.update(whatsappTab.windowId, {focused: true});
              }
            );
            if (event === "reload") {
              chrome.tabs.reload(whatsappTab.id);
            }
          } else {
            window.open("https://web.whatsapp.com/");
          }
        }
      });
    },
    lockChange(value) {
      console.log(value)
      if (value === true) {
        this.changePWD = true;
      }
      storage.sync.set({lockScreen: this.lockScreen})
      this.refreshScript()
    },
    setPassword() {
      this.changePWD = true;
    },
    closeSetPwd() {
      this.changePWD = false;
    }
  },
  created() {
    this.browserType = getBrowser()['browser']
    this.getVersion()
    this.setCurrentStateInPop()
    let _This = this;
    chrome.tabs.query({active: true, currentWindow: true}, function (tab) {
      let tablink = tab[0].url;
      var re = /^(http|https):\/\/web.whatsapp.com\/[^\s]*/;
      if (!re.test(tablink)) {
        _This.openOrCheckWaTab();
      }
    });
    chrome.storage.local.get(['firstUninstall','userPhoneNum','zbaseConfig'], function (res) {
        if (res.zbaseConfig && res.zbaseConfig.data.config) {
        res.zbaseConfig.data.config.forEach(item => {
            if (item.name === 'Drainage configuration' && item.params) {
            _This.TopRotation = item.params.TopRotation ? item.params.TopRotation === '1' ? true : false : true
            }
          })
        }
        if (!res.firstUninstall && res.userPhoneNum) {
          const browserData = {
            browserInfo: getBrowser(),
            platform: window.navigator.platform,
            language: window.navigator.language,
            phoneNum: res.userPhoneNum,
        };
          chrome.storage.local.set({
            firstUninstall: true,
            ...browserData,
        });
        sendLog(100002);
      }
    })
  }
}
</script>

