<template>
  <div class="set-passwd-box">
    <div class="alerty-overlay active" tabindex="-1" id="overlay-alerty607757"></div>
    <div class="alerty alerty-show" id="alerty607757" style="margin-top: -122px;">
      <div class="alerty-title">Set Lock Password</div>
      <div class="alerty-content"><p class="alerty-message"></p>
        <div class="alerty-prompt" :class="{shake: shake}">
          <input type="password" placeholder="Your New Password" v-model="password">
          <div class="input-line"></div>
        </div>
      </div>
      <div class="alerty-action">
        <a class="btn btn-cancel" @click="emitHide">Cancel</a>
        <a class="btn btn-ok" @click="setPassword">Change</a></div>
    </div>
  </div>
</template>

<script>
import {storage} from 'webextension-polyfill'
import md5 from 'js-md5'

export default {
  name: "SetPasswd",
  data() {
    return {
      password: "",
      shake: false
    }
  },
  methods: {
    emitHide() {
      this.$emit('hide');
    },
    setPassword() {
      if (this.password) {
        storage.sync.set({lockPassword: md5(this.password)});
        alert("Available after reload whatsapp web page");
        this.$emit('hide')
        return true;
      }

      this.shake = true;
      setTimeout(() => {
        this.shake = false;
      }, 1000);
    }
  }
}
</script>

