<template>
  <div class='common-bottom-href' style="display: flex;justify-content: center;padding-left: 18px;align-items: center">
    <p>
      <a href='#' @click='moreTool'>More Free Tools</a>
      &nbsp;
      <a href='#' @click='Reviews'>Reviews</a>
      &nbsp;
      <a href='#' @click='support'>Support</a>
      &nbsp;
      <a href='#' @click='share'>Share</a>
      &nbsp;
    </p>
  </div>
</template>

<script>
import {REVIEWS, SHARE, TOOLS_DOWNLOAD} from '../utils/config';
import {sendLog} from '@/utils/log-util';

export default {
  props: {
    sendType: {
      type: String,
      default: ''
    }
  },
  name: 'CommonBottomHref',
  data() {
    return {};
  },
  mounted() {
  },
  methods: {
    moreTool(){
      sendLog(100111);
      window.open(TOOLS_DOWNLOAD);
    },
    Reviews(){
      sendLog(100112);
      window.open(REVIEWS);
    },
    support() {
      sendLog(100113);
      chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
        chrome.storage.local.get(['zbaseConfig'], res => {
          let servicePhoneConf = res.zbaseConfig?.data?.config?.find(item=>item.name === 'servicePhone')?.params?.phone ?? '+8618976917166'
          chrome.tabs.sendMessage(tabs[0].id, {openChat: servicePhoneConf });
        })
      })
    },
    share() {
      sendLog(100114);
      window.open(SHARE)
    },
  }
}
</script>

